import React from 'react'
import { Link } from 'gatsby'

const Navbar = () => (
    <nav
        className="navbar"
        style={{
            // position: "absolute",
            // top: "0",
            // left: "0",
            // right: "0",
            // height: "100px",
            // zIndex: "2",
            // padding: "1rem 3rem",
            // display: "flex"
        }}
    >
        <div className="nav__link">
            <Link
                style={{
                    color: "white",
                    textDecoration: "none"
                }}
                to="/">
                Home
            </Link>
        </div>
        <div className="nav__link">
            <Link
                style={{
                    color: "white",
                    textDecoration: "none"
                }}
                to="/about">
                About
            </Link>
        </div>
    </nav>
);

export default Navbar;